<template>
  <section>
    <article
      v-for="(item, article) in articles"
      :key="article"
      @click="shareData(article)"
    >
      <img :src="item.screen" :title="item.title" :alt="item.title" />
      <h2>{{ item.title }}</h2>
    </article>
  </section>
</template>
<script>
export default {
  name: "Lab",
  data() {
    return {
      articles: [
        {
          title: "Mobile Teste",
          description: `Uma alternativa à inspeção de elementos do Google Chrome, por aqui considero 
          mais simples de se testar, e ainda foi incluso recursos onde você foca no projeto, removendo elementos 
          extra da tela e deixando só o que interessa que é o frontend. As resoluções disponíveis são as mesmas presentes no 
          Chrome e algumas extras como FullHD+.`,
          git: "https://github.com/renato7oliveira/mobile-teste",
          screen: require("../assets/images/portfolio-mobile-first.jpg"),
          url: "https://renato7oliveira.github.io/mobile-teste/src",
        },
        {
          title: "Beatles Tributo",
          description: `
          A ideia principal foi usar Animações com CSS3 puro. Então logo no header tem uma foto  vazada  
          com as capas de todos os álbuns de fundo. Abaixo tem uma grid com cabeças "pulantes" dos integrantes 
          retiradas do Pinterest.
          `,
          git: "https://github.com/renato7oliveira/beatles-tributo",
          screen: require("../assets/images/portfolio-beatles.jpg"),
          url: "https://renato7oliveira.github.io/beatles-tributo/src/index.html",
        },
        {
          title: "Street Fighter Tributo (old school)",
          description: `
          Trabalho realizado a caráter de pesquisa usando o Adobe Edge (R.I.P). Ele é todo interativo, 
          após o usuário escolher um personagem, sua música tema é tocada 
          e aparecem botões que disparam som dos personagens no clique do usuário. Na introdução tem um 
          vídeo de abertura, então o projeto ficou um pouco pesado, mas vale à pena conferir. As imagens 
          foram retiradas do Pinterest, fiz a diagramação e mínimos recortes.
          `,
          git: "https://github.com/renato7oliveira/street-fighter-audio-project",
          screen: require("../assets/images/portfolio-street-fighter.jpg"),
          url: "https://renato7oliveira.github.io/street-fighter-audio-project",
        },
      ],
    };
  },
  methods: {
    shareData(keyDetail) {
      this.$router.push({
        name: "PortfolioDetalhe",
        params: { data: this.articles[keyDetail] },
      });
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
};
</script>


<style lang="scss" scoped>
section {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  article {
    border-radius: 5px;
    transition: 0.3s;
    &:hover {
      cursor: pointer;
      transform: translateY(-5px);
    }
    &:active {
      transform: translateY(0px);
    }
    h2 {
      font-size: 0.9em;
      font-weight: normal;
      padding: 15px;
    }
    img {
      width: 100%;
      border-radius: 3px 3px 0 0;
    }
  }
}
/* MEDIA QUERIES START */
@media (max-width: 1024px) {
  section {
    grid-template-columns: 1fr;
    margin-bottom: 90px;
  }
}
/* MEDIA QUERIES END  */
</style>